import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-cls',
  templateUrl: './cls.component.html',
  styleUrls: ['./cls.component.css']
})
export class ClsComponent implements OnInit {

  value = 1
  normalized_value = 0
  max_slider_val = 10
  showHoverable = false

  @Input() z = 1;
  @Input() icon: string;
  @Input() showTrue: boolean;
  @Input() trueProb: number;
  @Output() valueChangeEvent = new EventEmitter<number>();

  constructor() { }

  ngOnInit(): void {
  }

  incr() {
    if (this.value >= this.max_slider_val) {
      return;
    }
    this.value += 1;
    this.valueChangeEvent.emit(this.value);
  }

  decr() {
    if (this.value <= 0) {
      return;
    }
    this.value -= 1;
    this.valueChangeEvent.emit(this.value);
  }

  changed(ev) {
    this.value = ev.value;
    this.valueChangeEvent.emit(this.value);
  }

  normalize_self(z) {
    this.normalized_value = this.value / z;
  }

}
